@media screen and (max-width: 767px) {
  .checkbox-other, .radio-other {
    max-width: 100%;
  }
}

.checkbox-other {
  max-width: 575px;
}

.checkbox-other input[type=checkbox], .radio-other input[type=radio] {
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
  width: 0;
  height: 0;
}

.checkbox-other .checkbox-other-text, .radio-other .radio-other__text {
  display: inline-block;
  position: relative;
  padding: 0 0 0 25px;
  color: #313131;
  font-size: 16px;
  letter-spacing: 0.05em;
}

.checkbox-other .checkbox-other-text:before {
  content: "";
  display: inline-block;
  width: 15px;
  height: 15px;
  position: absolute;
  left: 0;
  top: -3px;
  border: 1px solid #DBDBDB;
  border-radius: 3px;
}

.radio-other .radio-other__text:before {
  content: "";
  display: inline-block;
  width: 15px;
  height: 15px;
  position: absolute;
  left: 0;
  top: 3px;
  border: 1px solid #313131;
  border-radius: 50%;
}

/* Checked */
.checkbox-other input[type=checkbox]:checked + .checkbox-other-text:before {
  background: #313131;
  background-image: url(../images/radiobutton/checkbox.svg);
  background-position: top;
  background-repeat: no-repeat;
}

.radio-other input[type=radio]:checked + .radio-other__text:before {
  background-image: url(../images/radiobutton/radiobutton.svg);
  background-position: center;
  background-repeat: no-repeat;
}

